  background-color: #42b983;
  color: white;
  padding: 10px;
}

.center-container {
  display: flex;
  justify-content: center;
  gap: 45px;
  align-items: center;
}

@media (max-width: 768px) {
  .center-container {
    gap: 20px;
  }
}

.info-button {
  top: 10px;
  right: 10px;
}

@media only screen and (max-width: 768px) {
  /* For mobile devices, the position should be 0px */
  .info-button {
    position: absolute;
    top: 0px;
    right: 0px;
  }
}

#vanPositionContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 0 10px 10px 10px;
}

#map {
  flex-grow: 1; /* Allow the map to grow and take the remaining space */
  min-height: 200px; /* Set a minimum height so it never disappears */
  position: relative;
}

#FullMapApp {
  display: flex;
  flex-direction: column;
  height: 100%; /* Make sure the FullMapApp also takes up the available space */
}

#mapLayerControls {
  padding: 10px;
}

#timeframe {
  padding-top: 5px;
}

</style>
